import styled from 'styled-components';
import { BOOKMARK, SUBSCRIBE } from '@/constants/mypageData';

interface TabProps {
  activeTab: string;
  setActiveTab: (activeTab: string) => void;
}

function TapBtn({ activeTab, setActiveTab }: TabProps) {

  return (
    <TapBtnStyled>
      <button
        onClick={() => setActiveTab('subscribe')}
        className={activeTab === 'subscribe' ? 'active' : ''}
      >
        구독한 뉴스레터 ({SUBSCRIBE.length})
      </button>
      <button
        onClick={() => setActiveTab('subscribe')}
        className={activeTab === 'subscribe' ? 'active' : ''}
      >
        북마크한 뉴스레터 ({BOOKMARK.length})
      </button>
      <button
        onClick={() => setActiveTab('subscribe')}
        className={activeTab === 'subscribe' ? 'active' : ''}
      >
        뉴스레터 설정
      </button>
    </TapBtnStyled>
  );
}

const TapBtnStyled = styled.div`

    margin: 40px 0;
  border-bottom: 1px solid ${({ theme }) => theme.color.border};
    
    button {
        border: none;
        background-color: transparent;
    }
  
`;

export default TapBtn;